import { Component, OnInit } from '@angular/core';
import { NetworkService } from './network.service';
import { InstanceStatus } from './common/edit-data';
import { ConfigService } from './config.service';
import { Config } from './common/config';
import { environment } from 'src/environments/environment';
import * as _ from 'lodash';
import { Router } from '@angular/router';
import { DemoService } from './edit/demo/demo.service';
import { QuizService } from './edit/demo/quiz.service';
import { LocaleService } from './locale.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {

    public status: InstanceStatus;

    constructor(private network: NetworkService,
                private configService: ConfigService,
                private demo: DemoService,
                private L: LocaleService,
                private router: Router) { }

    ngOnInit() {
        this.configService.checkInstanceObservable.subscribe(_ => {
            this.checkInstance();
        });
        window.onresize = _.throttle(() => {
            this.router.navigate(['/login']);
            this.demo.init();
            alert(this.L.resizeMessage)
        }, 1000);
    }

    private checkInstance() {
        this.network.checkInstance(environment.mode)
            .then((status: InstanceStatus) => {
                this.status = status;
                if (this.status.active) {
                    this.configService
                        .setConfig(new Config(this.status.config));
                }
            });
    }
}
