import { Component, OnInit, Input, ViewChild, ElementRef, AfterViewInit, OnDestroy, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormArray, FormBuilder } from '@angular/forms';
import { AnnotationBox, WITHOUT_NUM_REG, NUM_REG, isNumberText } from '../../annotation-util';
import { KEY_MARK_LABEL, Overall, KEY_X_AXIS_LABEL, KEY_Y_AXIS_LABEL } from 'src/app/common/task';
import { MatSelectChange } from '@angular/material';
import { FeedbackService } from 'src/app/edit/common/feedback.service';
import { Feedback } from 'src/app/common/verification-data';
import { Subscription } from 'rxjs';
import { LocaleService } from 'src/app/locale.service';

import * as d3 from 'd3';

@Component({
    selector: 'app-axis-spec',
    templateUrl: './axis-spec.component.html',
    styleUrls: [
        './axis-spec.component.scss',
        '../overall.component.scss'
    ]
})
export class AxisSpecComponent implements OnInit, AfterViewInit, OnDestroy {

    @Input() auto: boolean;
    @Input() index: number;
    @Input() group: FormGroup;
    @Input() imgRef: ElementRef;
    @Input() task: Overall;
    @Input() texts: AnnotationBox[] = [];

    @ViewChild('title', { static: false }) titleRef: ElementRef;
    @ViewChild('dir', { static: false }) dirRef: ElementRef;
    @ViewChild('type', { static: false }) typeRef: ElementRef;
    @ViewChild('unit', { static: false }) unitRef: ElementRef;
    @ViewChild('labels', { static: false }) labelsRef: ElementRef;
    @ViewChild('markLabels', { static: false }) markLabelsRef: ElementRef;
    @ViewChild('wrapper', { static: false }) wrapper: ElementRef;

    @Output() scroll = new EventEmitter<number>();

    public hasMarkLabels = false;
    public focusedFeedbackKey = '';
    public focusedFeedback: Feedback;
    private refMap: { [key: string]: ElementRef };

    private sub: Subscription;
    public L: any;

    constructor(private fs: FeedbackService,
                private fb: FormBuilder,
        _L: LocaleService) {
            this.L = _L.axisSpec;
        }

    ngOnInit() {
        this.sub = this.fs.feedbackObservable.subscribe(feedback => {
            this.focusedFeedbackKey = '';
            this.focusedFeedback = null;
            if (feedback.taskKey !== this.task.key) {
                return;
            }
            const splited = feedback.annotationKey.split('.');
            if (splited[0] !== 'spec' ||
                splited[1] !== 'axis' ||
                +splited[2] !== this.index) {
                return;
            }

            this.focusedFeedbackKey = splited[3];
            this.focusedFeedback = feedback;
            setTimeout(() => {
                this.scrollEvent();
            }, 0);
        });

        this.hasMarkLabels = this.texts
            .filter(d => d.label.value === KEY_MARK_LABEL).length > 0;
        this.mapMarkLabelToAxis();
    }

    ngAfterViewInit() {
        this.refMap = {
            'title': this.titleRef,
            'dir': this.dirRef,
            'type': this.typeRef,
            'unit': this.unitRef,
            'labels': this.labelsRef,
            'markLabels': this.markLabelsRef,
        };
    }

    onValueTypeChange(change: MatSelectChange) {
        if (change.value === 'quantitative') {
            this.group.get('unit').setValue('integer');
            this.obtainValuePerPixel();
        } else {
            this.group.get('unit').setValue('');
        }
        this.mapMarkLabelToAxis();
    }

    scrollEvent() {
        const ref = this.refMap[this.focusedFeedbackKey];
        if (!ref) {
            return;
        }
        const el = ref.nativeElement as HTMLElement;
        const top = el.getBoundingClientRect().top;
        this.scroll.emit(top);
    }

    ngOnDestroy() {
        if (this.sub) {
            this.sub.unsubscribe();
        }
    }

    focusedElemWithKey(key: string): boolean {
        return !!this.focusedFeedback && this.focusedFeedbackKey === key;
    }

    private obtainValuePerPixel() {
        const dir = this.group.get('dir').value;
        const { width, height } = this.imgRef.nativeElement as HTMLImageElement;

        const axis = dir === 'x'
            ? this.texts.filter(text => text.label.value === KEY_X_AXIS_LABEL)
            : this.texts.filter(text => text.label.value === KEY_Y_AXIS_LABEL);
        const axisValues = axis.map(d => {
            return {
                value: +d.text.value.replace(WITHOUT_NUM_REG, ''),
                center: d.center
            };
        }).sort((a, b) => a.value - b.value);

        const valuesPerPixel: number[] = [];
        for (let i = 0; i < axisValues.length - 1; i++) {
            const curr = axisValues[i];
            const next = axisValues[i + 1];
            const interval = Math.abs(dir === 'x'
                ? (curr.center[0] - next.center[0]) * width
                : (curr.center[1] - next.center[1]) * height);
            valuesPerPixel.push((+next.value - +curr.value) / interval);
        }
        const valuePerPixel = +d3.mean(valuesPerPixel);
        this.group.get('valuePerPixel').setValue(valuePerPixel);
    }

    mapMarkLabelToAxis() {
        const markLabelBoxes = this.texts.filter(d => {
            return d.label.value === KEY_MARK_LABEL;
        });
        const group = this.group;
        const groupType = group.get('type').value as string;
        const markLabelsForm = group.get('markLabels') as FormArray;
        while (markLabelsForm.length > 0) {
            markLabelsForm.removeAt(0);
        }
        const filterInsertLabels = (s: string) => {
            if (groupType === 'quantitative') {
                const values = (group.get('labels') as FormArray)
                    .controls.map(d => {
                        return +d.value.replace(WITHOUT_NUM_REG, '');
                    });
                const extent = d3.extent(values);
                const value = +s.replace(WITHOUT_NUM_REG, '');
                return isNumberText(s) &&
                    value >= extent[0] && value <= extent[1];
            } else {
                const axisLabels = group.get('labels').value as string[];
                return axisLabels.includes(s);
            }
        };
        const insertedMarkLabels = markLabelBoxes.filter(box => {
            return filterInsertLabels(box.text.value);
        });

        insertedMarkLabels.filter(box => {
            markLabelsForm.push(this.fb.control(box.text.value));
        });
        group.get('labeled').setValue(insertedMarkLabels.length > 0);
    }
}
