import { Injectable } from '@angular/core';
import { AnnotationMeta } from 'src/app/common/edit-data';
import { NetworkService } from 'src/app/network.service';
import { FormGroup } from '@angular/forms';
import * as QUIZ_BAR_DATA from './quiz_bar_data.json';
import * as QUIZ_LINE_DATA from './quiz_line_data.json';
import { VOverallData, Review, Feedback } from 'src/app/common/verification-data.js';
import { Config } from 'src/app/common/config.js';

import * as d3 from 'd3';
import { KEY_X_AXIS_LABEL, KEY_X_AXIS_TITLE, KEY_Y_AXIS_LABEL, KEY_LEGEND_LABEL } from 'src/app/common/task/index.js';
import { environment } from 'src/environments/environment.js';

@Injectable({
    providedIn: 'root'
})
export class QuizService {

    public currReview: Review;

    private data: VOverallData;
    private chartType: 'bar' | 'line';
    private readonly textIOU = 0.5;
    private _params = {
        bar: {
            iou: 0.8,
            numXLabels: 4,
            numYLabels: 7
        },
        line: {
            iou: 0.3,
            numXLabels: 9,
            numYLabels: 7
        }
    };
    constructor(private network: NetworkService) {
        this.chartType = environment.chartType as 'bar' | 'line';
        this.data = this.chartType === 'bar'
            ? new VOverallData('quiz', QUIZ_BAR_DATA['default'])
            : new VOverallData('quiz', QUIZ_LINE_DATA['default']);
    }

    async getAnnotationElem(): Promise<[AnnotationMeta, Blob]> {
        const paths = await this.network.getPaths();
        const path = paths[0];
        const blob = await this.network.getImage(path);
        const meta = { path, feedbacks: [], annotation: null };
        return [new AnnotationMeta(meta), blob];
    }

    grade(path: string, formGroup: FormGroup, config: Config): [boolean, any] {
        const result = {};
        for (const task of config.tasks) {
            result[task.key] = task.getFormValue(formGroup);
        }
        const submitted = new VOverallData('key_0', result['key_0']);
        const feedbacks = [];
        let otherIndex = 0;

        if (submitted.marks.length !== this.data.marks.length) {
            feedbacks.push(new Feedback({
                taskKey: 'key_0',
                annotationKey: `mark.other.${otherIndex}`,
                comment: '   .'
            }));
            otherIndex += 1;
        }

        submitted.marks.forEach((mark, i) => {
            const maxIou = d3.max(this.data.marks, d => this.iou(mark.bound, d.bound));
            if (maxIou <= this.params.iou) {
                feedbacks.push(new Feedback({
                    taskKey: 'key_0',
                    annotationKey: `mark.${i}`,
                    comment: '   .'
                }));
            }
        });

        otherIndex = 0;

        const targetSpec = this.data.spec;
        const xLabelIndices = submitted.texts
            .map((d, i) => ([i, d.label === KEY_X_AXIS_LABEL]))
            .filter(([_, check]) => check)
            .map(d => d[0]) as number[];

        const xTitleIndices = submitted.texts
            .map((d, i) => ([i, d.label === KEY_X_AXIS_TITLE]))
            .filter(([_, check]) => check)
            .map(d => d[0]) as number[];

        const yLabelIndices = submitted.texts
            .map((d, i) => ([i, d.label === KEY_Y_AXIS_LABEL]))
            .filter(([i, check]) => check)
            .map(d => d[0]) as number[];

        if (xTitleIndices.length === 0) {
             feedbacks.push(new Feedback({
                taskKey: 'key_0',
                annotationKey: `text.other.${otherIndex}`,
                comment: 'X  .'
            }));
            otherIndex += 1;
        } else {
            const target = this.data.texts
                .find(d => d.label === KEY_X_AXIS_TITLE);
            const index = xTitleIndices[0];
            const title = submitted.texts[index];

            if (title.text !== target.text) {
                feedbacks.push(new Feedback({
                    taskKey: 'key_0',
                    annotationKey: `text.${index}`,
                    comment: 'X   .'
                }));
            } else if (this.iou(title.bound, target.bound) < this.textIOU) {
                feedbacks.push(new Feedback({
                    taskKey: 'key_0',
                    annotationKey: `text.${index}`,
                    comment: 'X   .'
                }));
            }
        }

        if (xLabelIndices.length !== this.params.numXLabels) {
            feedbacks.push(new Feedback({
                taskKey: 'key_0',
                annotationKey: `text.other.${otherIndex}`,
                comment: 'X   .'
            }));
            otherIndex += 1;
        } else {
            const targetTexts = this.data.texts
                .filter(d => d.label === KEY_X_AXIS_LABEL);

            xLabelIndices.forEach(index => {
                const text = submitted.texts[index];
                const maxIou = d3.max(targetTexts, d => this.iou(text.bound, d.bound));
                const ious = targetTexts
                    .map(d => this.iou(text.bound, d.bound));
                const maxIndex = ious.indexOf(Math.max.apply(null, ious));
                if (maxIou <= this.textIOU) {
                    feedbacks.push(new Feedback({
                        taskKey: 'key_0',
                        annotationKey: `text.${index}`,
                        comment: '  .'
                    }));
                } else if (targetTexts[maxIndex].text !== text.text) {
                    feedbacks.push(new Feedback({
                        taskKey: 'key_0',
                        annotationKey: `text.${index}`,
                        comment: '  .'
                    }));
                }
            });
        }

        if (yLabelIndices.length !== this.params.numYLabels) {
            feedbacks.push(new Feedback({
                taskKey: 'key_0',
                annotationKey: `text.other.${otherIndex}`,
                comment: 'Y   .'
            }));
            otherIndex += 1;
        } else {
            const targetTexts = this.data.texts
                .filter(d => d.label === KEY_Y_AXIS_LABEL);

            yLabelIndices.forEach(index => {
                const text = submitted.texts[index];
                const ious = targetTexts
                    .map(d => this.iou(text.bound, d.bound));
                const maxIndex = ious.indexOf(Math.max.apply(null, ious));
                const maxIou = ious[maxIndex];
                if (maxIou <= this.textIOU) {
                    feedbacks.push(new Feedback({
                        taskKey: 'key_0',
                        annotationKey: `text.${index}`,
                        comment: '  .'
                    }));
                } else if (targetTexts[maxIndex].text !== text.text) {
                    feedbacks.push(new Feedback({
                        taskKey: 'key_0',
                        annotationKey: `text.${index}`,
                        comment: '  .'
                    }));
                }
            });
        }

        const legendIndices = submitted.texts
            .map((d, i) => ([i, d.label === KEY_LEGEND_LABEL]))
            .filter(([_, check]) => check)
            .map(d => d[0]) as number[];

        if (this.chartType === 'line') {

            if (legendIndices.length !== 2) {
                feedbacks.push(new Feedback({
                    taskKey: 'key_0',
                    annotationKey: `text.other.${otherIndex}`,
                    comment: '   .'
                }));
                otherIndex += 1;
            } else {
                const targetTexts = this.data.texts
                    .filter(d => d.label === KEY_LEGEND_LABEL);
                legendIndices.forEach(index => {
                    const text = submitted.texts[index];
                    const maxIou = d3.max(targetTexts, d => this.iou(text.bound, d.bound));
                    const ious = targetTexts
                        .map(d => this.iou(text.bound, d.bound));
                    const maxIndex = ious.indexOf(Math.max.apply(null, ious));
                    if (maxIou <= this.textIOU) {
                        feedbacks.push(new Feedback({
                            taskKey: 'key_0',
                            annotationKey: `text.${index}`,
                            comment: '  .'
                        }));
                    } else if (targetTexts[maxIndex].text !== text.text) {
                        feedbacks.push(new Feedback({
                            taskKey: 'key_0',
                            annotationKey: `text.${index}`,
                            comment: '  .'
                        }));
                    }
                });
            }
        }

        const checkAxis = (index: number) => {
            const axis = submitted.spec.axes[index];
            const target = this.data.spec.axes[index];

            if (axis.title.value !== target.title.value) {
                feedbacks.push(new Feedback({
                    taskKey: 'key_0',
                    annotationKey: `spec.axis.${index}.title`,
                    comment: '  .'
                }));
            }

            if (axis.type.value !== target.type.value) {
                feedbacks.push(new Feedback({
                    taskKey: 'key_0',
                    annotationKey: `spec.axis.${index}.type`,
                    comment: '   .'
                }));
            }

            const labels = axis.labels.value.sort();
            const targetLabels = target.labels.value.sort();
            const notSame = labels.length !== targetLabels.length ||
                labels.some((d, i) => {
                    return i >= targetLabels.length || targetLabels[i].trim() !== d.trim();
                });
            if (notSame) {
                feedbacks.push(new Feedback({
                    taskKey: 'key_0',
                    annotationKey: `spec.axis.${index}.labels`,
                    comment: '  .'
                }));
            }

            if (axis.unit.value !== target.unit.value) {
                feedbacks.push(new Feedback({
                    taskKey: 'key_0',
                    annotationKey: `spec.axis.${index}.unit`,
                    comment: '    .'
                }));
            }

            if (axis.encoding.value !== target.encoding.value) {
                feedbacks.push(new Feedback({
                    taskKey: 'key_0',
                    annotationKey: `spec.axis.${index}.encoding`,
                    comment: '   .'
                }));
            }
        };

        otherIndex = 0;
        if (submitted.spec.axes.length !== 2) {
            feedbacks.push(new Feedback({
                taskKey: 'key_0',
                annotationKey: `spec.axis.other.${otherIndex}`,
                comment: ' X, Y  1  .'
            }));
            otherIndex += 1;
        } else {
            checkAxis(0);
            checkAxis(1);
        }

        otherIndex = 0;
        const rows = submitted.raw.rows;
        const targetRows = this.data.raw.rows;

        if (submitted.spec.axes.length === 2) {
            const xKey = submitted.spec.axes[0].title.value;
            const yKey = submitted.spec.axes[1].title.value;
            const targetXKey = targetSpec.axes[0].title.value;
            const targetYKey = targetSpec.axes[1].title.value;
            if (this.chartType === 'bar') {
                rows.forEach((row, i) => {
                    const x = row.value[xKey];
                    const y = row.value[yKey];

                    const targetRow = targetRows.find(d => {
                        return d.value[targetYKey] === y;
                    });
                    if (!targetRow) {
                        feedbacks.push(new Feedback({
                            taskKey: 'key_0',
                            annotationKey: `raw.${i}`,
                            comment: '   .'
                        }));
                    } else if (isNaN(+x) ||
                        Math.abs(+targetRow.value[targetXKey] - +x) > 0.2) {
                        feedbacks.push(new Feedback({
                            taskKey: 'key_0',
                            annotationKey: `raw.${i}`,
                            comment: '   .'
                        }));
                    }
                });
            } else {
                const legendKey = submitted.spec.legend.title.value;
                const targetLegendKey = targetSpec.legend.title.value;
                rows.forEach((row, i) => {
                    const x = row.value[xKey];
                    const y = isNaN(+row.value[yKey]) ? 0 : +row.value[yKey];
                    const legend = row.value[legendKey];

                    const filteredRows = targetRows
                        .filter(d => d.value[targetXKey] === x)
                        .sort((a, b) => {
                            const aDist = Math.abs(+a.value[targetYKey] - y);
                            const bDist = Math.abs(+b.value[targetYKey] - y);
                            return aDist - bDist;
                        });


                    if (filteredRows.length === 0) {
                        feedbacks.push(new Feedback({
                            taskKey: 'key_0',
                            annotationKey: `raw.${i}`,
                            comment: '   .'
                        }));
                    } else {
                        const targetRow = filteredRows[0];
                        const dist = Math.abs(+targetRow.value[targetYKey] - y);
                        const targetLegend = targetRow.value[targetLegendKey];
                        if (dist > 5 || targetLegend !== legend) {
                            feedbacks.push(new Feedback({
                                taskKey: 'key_0',
                                annotationKey: `raw.${i}`,
                                comment: '   .'
                            }));
                        }
                    }
                });
            }

        }

        this.currReview = new Review({
            path,
            approved: feedbacks.length === 0,
            feedbacks
        });

        return [feedbacks.length === 0, result];
    }

    private iou(a: number[], b: number[]) {
        a[2] = Math.max(a[2], a[0]);
        a[3] = Math.max(a[3], a[1]);
        b[2] = Math.max(b[2], b[0]);
        b[3] = Math.max(b[3], b[1]);
        const rightX1 = Math.max(a[0], b[0]);
        const leftX2 = Math.min(a[2], b[2]);
        const topY2 = Math.min(a[3], b[3]);
        const bottomY1 = Math.max(a[1], b[1]);

        const inter = Math.max((leftX2 - rightX1), 0) *
            Math.max((topY2 - bottomY1), 0);
        const areaA = (a[2] - a[0]) * (a[3] - a[1]);
        const areaB = (b[2] - b[0]) * (b[3] - b[1]);
        return inter / (areaA + areaB - inter);
    }

    get params() {
        return this._params[this.chartType];
    }

}
