import { Component, OnInit, Input, AfterViewInit, Output, EventEmitter } from '@angular/core';
import { EditService } from 'src/app/edit.service';
import { ImageElem, ModelInfo, Progress } from 'src/app/common/edit-data';
import { MatCheckboxChange } from '@angular/material';

import { NetworkService } from 'src/app/network.service';
import { Observable } from 'rxjs';
import { ConfigService } from 'src/app/config.service';
import { Config } from 'src/app/common/config';
import { LocaleService } from 'src/app/locale.service';
import { TimerService } from '../timer.service';
import { EditMode } from '../edit.component';

@Component({
    selector: 'app-side-nav',
    templateUrl: './side-nav.component.html',
    styleUrls: ['./side-nav.component.scss']
})
export class SideNavComponent implements OnInit, AfterViewInit {

    @Input() mode: EditMode;
    @Input() event: Observable<string>;
    public autoListShow = true;
    public imageListShow = true;
    public progress: Progress;
    public modelInfos: ModelInfo[];
    public userName = '';
    public userProgress = 0;
    public config: Config;

    @Output() remove = new EventEmitter<void>();

    constructor(public edit: EditService,
                private network: NetworkService,
                private cs: ConfigService,
                public timer: TimerService,
                public L: LocaleService) { }

    ngOnInit() {
        this.event.subscribe(key => {
            if (key === 'progress') {
                this.updateProgress();
                this.updateModelInfo();
            }
        });

        this.cs.configObservable.subscribe(config => {
            this.config = config;
        });

        this.cs.userNameObservable.subscribe(name => {
            this.userName = name;
        });
    }

    ngAfterViewInit() {
        this.updateProgress();
    }

    selectImage(image: ImageElem) {
        this.edit.imageSubject.next(image);
        this.updateModelInfo();
    }

    removeImage() {
        this.edit.ignoreImage()
            .then(_ => {
                this.remove.emit();
            });
    }

    // for debug
    async getRandImage() {
        await this.network.clearPending();
        const result = await this.network.getRandomElem();
        if (result) {
            const [meta, blob] = result;
            this.edit.parseImage(blob, meta.path, meta);
        } else {
            this.edit.removeImage();
        }
    }

    onChangeAutoComplete(change: MatCheckboxChange, modelInfo: ModelInfo) {
        this.edit.updateAutoComplete(modelInfo, change.checked);
    }

    private async updateProgress() {
        this.progress = await this.network.getProgress();
        if (this.config.sharedDataset) {
            this.userProgress = await this.network.getUserProgress();
        }
    }

    private updateModelInfo() {
        this.network.getModelInfo()
            .then(modelInfos => {
                if (!this.modelInfos) {
                    this.modelInfos = modelInfos;
                } else {
                    modelInfos.forEach((newModelInfo, i) => {
                        if (i < this.modelInfos.length) {
                            this.modelInfos[i].score = newModelInfo.score;
                        } else {
                            this.modelInfos.push(newModelInfo);
                        }
                    });
                }
            });
    }

    get imageName() {
        const splited = this.edit.image.path.split('/');
        return splited[splited.length - 1];
    }
}
