import { Component, Inject } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { LocaleService } from 'src/app/locale.service';

@Component({
    selector: 'app-review-send-dialog',
    templateUrl: './review-send-dialog.component.html'
})
export class ReviewSendDialogComponent {

    public L: any;
    public ok: 'ok';
    constructor(
        private dialogRef: MatDialogRef<ReviewSendDialogComponent>,
        @Inject(MAT_DIALOG_DATA) public data: any,
        _L: LocaleService) {
        this.L = _L.reviewSendDialog;
    }

    onClose() {
        this.data.response = '';
        this.dialogRef.close(this.data);
    }
}
